.productListEdit{
    border: none;
    border-radius: 10px;
    background-color: rgba(172, 105, 218, 0.541);
    padding: 5px 10px;
    cursor: pointer;
    margin-right: 20px;
    color: white;
}

.productListDelete{
    color: red;
    cursor: pointer;

}

.addproduct{
    border: none;
    border-radius: 10px;
    background-color: rgba(172, 105, 218, 0.541);
    padding: 5px 10px;
    cursor: pointer;
    margin-right: 20px;
    color: white;
    margin-top: 10px;
    margin-bottom: 10px;
}