@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "PT Sans", sans-serif;
}

.viewcard {
    width: 15rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    box-shadow: 0px 0px 5px rgba(128, 128, 128, 0.3);
    float: left;
    align-items: center;
    margin: 10px 0 10px 10px;
    position: relative;
    padding: 10px;
    border-radius: 10px;
    animation-name: fall;
    animation-duration: 300ms;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.viewcard:hover {
    animation-name: rise;
    animation-duration: 300ms;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.viewcard {
    text-decoration: none;
}

.categorycard > img {
    width: 50px;
}
@keyframes rise {
    to {
        transform: translate(0,-2%);
        z-index: 1;
        box-shadow: 0 0 10px lightskyblue;
    }
}
@keyframes fall {
    to {
        /* transform: translate(-1%); */
        z-index: 0;
    }
    from {
        transform: translate(0,-2%);
        z-index: 1;
        box-shadow: 0 0 10px lightskyblue;
    }
}

.container-outer {
    padding: 10px;
    /* background-color: aliceblue; */
}

.card-container::after, .section-title-container::after {
    content: "";
    display: table;
    clear: both;
}


.section-title {
    text-align: left;
    font-weight: bold;
    float: left;
}

.view-all-btn {
    float: right;
    border: none;
}

.vertical-scroll {
    display: grid;
    grid-auto-flow: column;
    overflow-y: auto;
    box-sizing: content-box;
    scrollbar-width: none;
}
.vertical-scroll::-webkit-scrollbar {
    display: none;
}
.filter-col {
    box-shadow: 0 0 5px rgba(128, 128, 128, 0.3);
}
.filter-section {
    /* border-bottom: 1px solid; */
    padding: 20px;
}
@media screen and (min-width: 890px) {
    .filters {
        /* position: fixed; */
    }
}
.product-pane {
    box-shadow: 0 0 10px rgba(128, 128, 128, 0.3);
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    width: 97%;
}

.product-info, .product-info > * {
    text-align: left;
    margin-top: 10px;
    margin-left: 10px;
}

@media screen and (min-width:992px) {
    .sidebar {
        width: 34%;
        height: 100vh;
        float: left;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
    }
    .sidebar::-webkit-scrollbar {
        display: none;
    }
    .product-pane {
        width: 64%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        /* height: 85vh; */
        float: left;
    }
}
.product::after {
    clear: both;
    display: table;
}

@media screen and (max-width:600px) {
    .img-container {
        width: 100%;
    }
    .product-info {
        width: 95%;
    }
}
