.msg-bot {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    background-color: #A8DDFD;
    border: 1px solid #97C6E3;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 8px;
}

.msg-bot::after {
    content: "";
    position: absolute;
    border-top: 15px solid #A8DDFD;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    top: 0px;
    left: -15px;
}

.msg-bot::before {
    content: "";
    position: absolute;
    border-top: 17px solid #97C6E3;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    top: -1px;
    left: -17px;
}

.msg-user {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    background-color: #f8e896;
    border: 1px solid #dfd087;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 8px;
    margin-left: auto;
}

.msg-user::after {
    content: "";
    position: absolute;
    border-top: 15px solid #f8e896;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    top: 0px;
    right: -15px;
}

.msg-user::before {
    content: "";
    position: absolute;
    border-top: 17px solid #dfd087;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    top: -1px;
    right: -17px;
}
